import React, { Component, Fragment } from 'react';
import {
  getRevenueAndExpeditureAmountInCurrentYear,
} from 'services/Financequicklink';
import { Row, Col } from 'antd';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

export default class QuicklinkFinance extends Component {
  state = {
    dataQuicklinkFinance: [],
  };

  componentDidMount = async () => {
    const res = await getRevenueAndExpeditureAmountInCurrentYear();
    this.setState({
      dataQuicklinkFinance: res.data.columns,
    });
  };

  render() {
    const { dataQuicklinkFinance } = this.state;
    return (
      <Fragment>
        <Row gutter={24}>
          {dataQuicklinkFinance.map((item, index) => (
            <Col span={6} key={item.label}>
              <div>
                <span style={{ backgroundColor: COLORS[index % 8], color: '#fff' }} className='infoCard'>
                  <span className="infoCard__digit"><i className='icmn-coin-dollar' /></span>
                  <span className="infoCard__desc">
                    <span style={{ color: '#fff' }} className="infoCard__title infoCart_link">
                      {item.label}
                    </span>
                    <p>Tng: {item.value}</p>
                  </span>
                </span>
              </div>
            </Col>
          ))}
        </Row>
      </Fragment>
    );
  }
}



