import React from 'react';
import Page from 'components/LayoutComponents/Page';
import Helmet from 'react-helmet';
import FinanceBeta from './FinanceBeta';

class FinanceHomepagePage extends React.Component {
  static defaultProps = {
    pathName: '',
    roles: ['agent', 'administrator'],
  };

  render() {
    return (
      <Page {...this.props}>
        <Helmet title="" />
        <FinanceBeta />
      </Page>
    );
  }
}

export default FinanceHomepagePage;



