import { request } from 'utils';

const prefix = '/api/finance';

export const getRevenueAndExpeditureAmountInCurrentYear = ({ query } = {}) =>
  request({
    prefix,
    url: '/QuickLink/GetRevenueAndExpeditureAmountInCurrentYear',
    method: 'GET',
    params: { query },
    data: {},
  });



