import { request } from 'utils';

const prefixFinance = '/api/finance';

export const getChartCreditPriceByFeeObjectNTrainType = ({ query } = {}) =>
  request({
    prefix: prefixFinance,
    url: '/Chart/GetChartCreditPriceByFeeObjectNTrainType',
    method: 'GET',
    params: { query },
    data: {},
  });

export const getChartCreditAmountByTrainType = ({ query } = {}) =>
  request({
    prefix: prefixFinance,
    url: '/Chart/GetChartCreditAmountByTrainType',
    method: 'GET',
    params: { query },
    data: {},
  });

